import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, message } from 'antd';
import {
  getPrizeClassificationById,
  updatePrizeClassification,
  createPrizeClassification,
} from 'services/prizeClassification';

@Form.create({ name: 'formScholarshipRatings' })
class FormScholarshipRatingsPage extends Component {
  state = {
    id: false,
    initialValue: {},
  };

  componentDidMount = async () => {
    const { match } = this.props;
    if (match.params.id) {
      const prizeClassificationByID = await getPrizeClassificationById(match.params.id);
      const initialValue = this.handleInitialValue(prizeClassificationByID.data);
      this.setState({ id: match.params.id, initialValue });
    }
  };

  handleInitialValue = initialValue => {
    const initialValueDefault = {
      id: '',
      label: '',
      gradeIn10: '',
      gradeIn4: '',
      gradeInPractice: '',
    };
    return { ...initialValueDefault, ...initialValue };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        // edit
        if (this.state.id) {
          values.id = this.state.id;
          updatePrizeClassification(values.id, values).then(res => {
            console.log('res update', res);
            if (res.status === 200) {
              message.success('Cập nhật XL học bổng thành công.');
              setTimeout(() => this.props.history.goBack(), 3000);
            } else {
              message.error(res.message);
            }
          });
        } else {
          values.gradeIn10 = parseInt(values.gradeIn10);
          values.gradeIn4 = parseInt(values.gradeIn4);
          values.gradeInPractice = parseInt(values.gradeInPractice);
          createPrizeClassification(values).then(res => {
            console.log('res create', res);
            if (res.status === 200) {
              message.success('Tạo XL học bổng thành công.');
              setTimeout(() => this.props.history.goBack(), 3000);
            } else {
              message.error(res.message);
            }
          });
        }
      } else {
        console.log('err form', err);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { scholarshipRatingsId } = this.state;
    return (
      <Card title={scholarshipRatingsId ? 'Cập nhật xếp loại học bổng' : 'Thêm xếp loại học bổng'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item {...formItemLayout} label="Mã xếp loại" hasFeedback>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên xếp loại" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm HT 10" hasFeedback>
              {getFieldDecorator('gradeIn10', {
                initialValue: initialValue.gradeIn10,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm HT 4" hasFeedback>
              {getFieldDecorator('gradeIn4', {
                initialValue: initialValue.gradeIn4,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm RL" hasFeedback>
              {getFieldDecorator('gradeInPractice', {
                initialValue: initialValue.gradeInPractice,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormScholarshipRatingsPage {...props} />
  </Page>
));



