import { request } from 'utils';

const prefix = '/api/categories';

export const getPrizeClassification = () =>
  request({
    prefix,
    url: '/PrizeClassification',
    method: 'GET',
    params: {},
    data: {},
  });
export const getPrizeClassificationById = id =>
  request({
    prefix,
    url: `/PrizeClassification/GetById`,
    method: 'GET',
    params: { id },
    data: {},
  });
export const createPrizeClassification = data =>
  request({
    prefix,
    url: '/PrizeClassification',
    method: 'POST',
    params: {},
    data,
  });
export const updatePrizeClassification = (id, data) =>
  request({
    prefix,
    url: `/PrizeClassification`,
    method: 'PUT',
    params: {},
    data: { ...data, id },
  });
export const deletePrizeClassification = id =>
  request({
    prefix,
    url: `/PrizeClassification`,
    method: 'DELETE',
    params: {},
    data: { id },
  });



